import { Supply, Allocation } from '@/types/schema';

interface PrintLabelProps {
    supply: Supply;
    allocation: Allocation;
}

export default function PrintLabel({ supply, allocation }: PrintLabelProps) {
    return (
        <div id="print-area" style={{
            width: '100mm',
            height: '60mm',
            border: '1px solid black',
            padding: '5mm',
            fontFamily: 'sans-serif',
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'space-between'
        }}>
            {/* Header: Product & Grade */}
            <div style={{ display: 'flex', justifyContent: 'space-between', borderBottom: '1px solid black', paddingBottom: '2mm' }}>
                <span style={{ fontSize: '14px' }}>{supply.productName}</span>
                <span style={{ fontSize: '14px', fontWeight: 'bold' }}>Grade: A / Class: L</span>
                {/* Note: Grade/Class are inside 'items' in the original JSON but flattened in Supply. 
                    For now, hardcoding or we need to expand Supply schema to store them. 
                    Task-1 Schema didn't have grade/class in Supply root. 
                    Assuming for this MVP we just show product name or add fields later. 
                    I'll keep it simple as requested. */}
            </div>

            {/* Main: Customer Name (Huge) */}
            <div style={{ flex: 1, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                <h1 style={{ fontSize: '32px', fontWeight: 'bold', margin: 0, textAlign: 'center' }}>
                    {allocation.customerName}
                </h1>
            </div>

            {/* Footer: Quantity & Barcode */}
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'end', borderTop: '1px solid black', paddingTop: '2mm' }}>
                <div>
                    <span style={{ fontSize: '12px' }}>Qty:</span>
                    <span style={{ fontSize: '24px', fontWeight: 'bold', marginLeft: '5px' }}>{allocation.allocatedQuantity}</span>
                </div>
                <div style={{ textAlign: 'right' }}>
                    {/* Dummy Barcode */}
                    <div style={{ height: '10mm', width: '40mm', background: 'repeating-linear-gradient(90deg, black, black 1px, white 1px, white 3px)' }}></div>
                    <span style={{ fontSize: '10px' }}>{allocation.id?.substring(0, 8)}</span>
                </div>
            </div>
        </div>
    );
}
