'use client';

import { useState } from 'react';
import useSWR from 'swr';
import { AgGridReact } from 'ag-grid-react';
import { ColDef, ModuleRegistry, AllCommunityModule } from 'ag-grid-community';
import 'ag-grid-community/styles/ag-grid.css';
import 'ag-grid-community/styles/ag-theme-alpine.css';
import { Supply } from '@/types/schema';
import AllocationForm from './AllocationForm';

// Register all community modules to prevent missing module errors (like #239)
ModuleRegistry.registerModules([AllCommunityModule]);

const fetcher = (url: string) => fetch(url).then((res) => res.json());

export default function StockManager() {
    const { data: supplies, error, isLoading } = useSWR<Supply[]>('/api/supplies', fetcher, { refreshInterval: 1000 });
    const [selectedSupplyId, setSelectedSupplyId] = useState<string | null>(null);

    const columnDefs: ColDef<Supply>[] = [
        { field: 'productName', headerName: 'Product Name', flex: 1 },
        { field: 'currentQuantity', headerName: 'Current Qty', flex: 1 },
        { field: 'status', headerName: 'Status', flex: 1 },
        { field: 'id', headerName: 'ID', hide: true },
    ];

    if (error) return <div>Failed to load supplies</div>;
    if (isLoading) return <div>Loading...</div>;

    return (
        <div style={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
            <div className="ag-theme-alpine" style={{ height: '400px', width: '100%' }}>
                <AgGridReact
                    theme="legacy"
                    rowData={supplies}
                    columnDefs={columnDefs}
                    rowSelection="single"
                    onRowClicked={(event) => setSelectedSupplyId(event.data?.id || null)}
                    animateRows={true}
                />
            </div>
            <AllocationForm selectedSupplyId={selectedSupplyId} supplies={supplies} />
        </div>
    );
}
