'use client';

import { useState } from 'react';
import { mutate } from 'swr';
import { Supply, Allocation } from '@/types/schema';
import PrintLabel from './PrintLabel';
import CustomerCombobox from './CustomerCombobox';

interface AllocationFormProps {
    selectedSupplyId: string | null;
    supplies?: Supply[];
}

export default function AllocationForm({ selectedSupplyId, supplies }: AllocationFormProps) {
    const [customerName, setCustomerName] = useState('');
    const [quantity, setQuantity] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [message, setMessage] = useState('');

    // Print State
    const [showPrintModal, setShowPrintModal] = useState(false);
    const [printData, setPrintData] = useState<{ supply: Supply, allocation: Allocation } | null>(null);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!selectedSupplyId) return;

        setIsSubmitting(true);
        setMessage('');

        // Capture current values for print before clearing
        const currentCustomer = customerName;
        const currentQuantity = Number(quantity);

        try {
            const res = await fetch('/api/allocate', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    supplyId: selectedSupplyId,
                    customerName: currentCustomer,
                    quantity: currentQuantity,
                }),
            });

            const data = await res.json();

            if (!res.ok) {
                throw new Error(data.error || 'Allocation failed');
            }

            setMessage('Allocation successful!');
            setCustomerName('');
            setQuantity('');

            mutate('/api/supplies');

            // --- Show Print Modal ---
            const currentSupply = supplies?.find(s => s.id === selectedSupplyId);
            if (currentSupply) {
                setPrintData({
                    supply: currentSupply,
                    allocation: {
                        id: data.allocationId,
                        supplyId: selectedSupplyId,
                        customerName: currentCustomer,
                        allocatedQuantity: currentQuantity,
                        allocatedAt: { seconds: Date.now() / 1000, nanoseconds: 0 } as any
                    }
                });
                setShowPrintModal(true);
            }

        } catch (error: any) {
            setMessage(`Error: ${error.message}`);
        } finally {
            setIsSubmitting(false);
        }
    };

    const handlePrint = () => {
        window.print();
    };

    const handleClose = () => {
        setShowPrintModal(false);
        setPrintData(null);
    };

    if (!selectedSupplyId) {
        return <div style={{ padding: '20px', border: '1px solid #ccc' }}>Select a supply from the grid to allocate.</div>;
    }

    return (
        <>
            <div style={{ padding: '20px', border: '1px solid #ccc', marginTop: '20px' }}>
                <h3>Allocation Form</h3>
                <form onSubmit={handleSubmit} style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
                    <div>
                        <label>Supply ID: </label>
                        <strong>{selectedSupplyId}</strong>
                    </div>


                    <div>
                        <label>Customer Name: </label>
                        <CustomerCombobox
                            value={customerName}
                            onChange={setCustomerName}
                        />
                    </div>
                    <div>
                        <label>Quantity: </label>
                        <input
                            type="number"
                            value={quantity}
                            onChange={(e) => setQuantity(e.target.value)}
                            required
                            min="1"
                            style={{ border: '1px solid black', padding: '4px' }}
                        />
                    </div>
                    <button
                        type="submit"
                        disabled={isSubmitting}
                        style={{ border: '1px solid black', padding: '8px', background: '#eee', cursor: 'pointer' }}
                    >
                        {isSubmitting ? 'Allocating...' : 'Confirm Allocation'}
                    </button>
                </form>
                {message && <p style={{ marginTop: '10px', fontWeight: 'bold' }}>{message}</p>}
            </div>

            {/* Print Preview Modal */}
            {showPrintModal && printData && (
                <div style={{
                    position: 'fixed', top: 0, left: 0, width: '100%', height: '100%',
                    background: 'rgba(0,0,0,0.5)', display: 'flex', alignItems: 'center', justifyContent: 'center',
                    zIndex: 1000
                }}>
                    <div style={{ background: 'white', padding: '20px', maxWidth: '500px', width: '100%' }}>
                        <h2>Print Preview</h2>
                        <div style={{ margin: '20px 0', border: '1px dashed gray', padding: '10px', display: 'flex', justifyContent: 'center' }}>
                            {/* This component is visible in modal AND used for printing */}
                            <PrintLabel supply={printData.supply} allocation={printData.allocation} />
                        </div>
                        <div style={{ display: 'flex', gap: '10px', justifyContent: 'flex-end' }}>
                            <button onClick={handleClose} style={{ padding: '10px 20px', cursor: 'pointer' }}>Close</button>
                            <button onClick={handlePrint} style={{ padding: '10px 20px', background: 'black', color: 'white', cursor: 'pointer' }}>Print Label</button>
                        </div>
                    </div>
                </div>
            )}
        </>
    );
}
